<template>
  <Modal v-model="isShowModal" title="Export Active Data">
    <template #default>
      <div>
        <div
          v-if="disabled && disabledReason"
          class="flex items-center bg-orange-lighten text-orange-04 px-4 py-3 rounded-lg gap-3 mb-4"
        >
          <i class="material-icons">warning</i>
          <p class="inline-block">
            {{ disabledReason }}
          </p>
        </div>
        <div
          class="flex items-center bg-blue-lighten text-blue-04 px-4 py-3 rounded-lg gap-4"
        >
          <p class="inline-block">
            Catalog Data is also available through our API
          </p>
          <a
            href="https://docs.reusely.com/reference/reusely-api-overview"
            rel="noreferrer noopener"
            target="_blank"
            class="text-blue-04 hover:text-blue-03"
            ><i class="material-icons">open_in_new</i></a
          >
        </div>
        <div class="grid gap-4 my-4">
          <Input
            :model-value="exportFilename"
            :disabled="true"
            placeholder="report name"
            label="Report Name"
          />
          <div class="mb-2">
            <label>Export as</label>
            <RadioGroup v-model="selectedFormat" :class="['radio--full']">
              <RadioButton
                v-for="fileFormat in fileFormats"
                :key="fileFormat.key"
                :label="fileFormat.key"
                class="cursor-pointer"
              >
                {{ fileFormat.label }}
              </RadioButton>
            </RadioGroup>
          </div>
        </div>
        <div class="flex items-start gap-4 my-4">
          <i class="material-icons text-teal-04">info</i>
          <p>
            <span class="font-semibold">Important Notes:</span> Download a CSV
            or XML file of the items you enabled in your Catalog. This file
            won't include conditions or prices columns. To download a file with
            your items conditions and prices, please go to your
            <a
              href="#"
              @click.prevent="
                $router.push({
                  name: 'integrationDataFeed'
                })
              "
              class="text-blue-04 hover:text-blue-03"
            >
              Data Feed Settings </a
            >.
          </p>
        </div>
      </div>
    </template>
    <template #footer>
      <div class="flex justify-between items-center">
        <a
          href="#"
          @click.prevent="closeModal"
          class="text-teal-04 hover:text-teal-03 py-4 px-6"
          >Cancel</a
        >
        <Button
          @click="handleExport"
          :loading="loading"
          :disabled="disabled"
          color="teal"
        >
          <span> <i class="material-icons">download</i> Export </span>
        </Button>
      </div>
    </template>
  </Modal>
</template>
<script>
import { defineComponent, computed, reactive } from 'vue'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'DeviceExportActiveData',
  components: {
    Modal,
    Button,
    Input
  },
  props: {
    exportFormats: {
      type: Array,
      default: () => ['csv', 'xml']
    },
    exportFilename: {
      type: String,
      default: 'Items Catalog'
    },
    format: {
      type: String,
      default: 'csv'
    },
    loading: {
      type: Boolean,
      default: false
    },
    show: {
      type: Boolean
    },
    disabled: {
      type: Boolean,
      default: false
    },
    disabledReason: {
      type: String
    }
  },
  emits: ['update:format', 'update:show', 'export'],
  setup(props, { emit }) {
    const state = reactive({
      isShowModal: false
    })
    const isShowModal = computed({
      get: () => (props.show ? props.show : state.isShowModal),
      set: (isShow) => {
        state.isShowModal = isShow
        emit('update:show', isShow)
      }
    })
    const selectedFormat = computed({
      get: () => props.format,
      set: (format) => {
        emit(
          'update:format',
          typeof format === 'string' ? format.toLowerCase() : format
        )
      }
    })
    const fileFormats = computed(() => {
      return props.exportFormats.map((fileFormat) => ({
        key: fileFormat,
        label:
          typeof fileFormat === 'string' ? fileFormat.toUpperCase() : fileFormat
      }))
    })

    const closeModal = () => (isShowModal.value = false)
    const handleExport = () => {
      emit('export', true)
    }

    return {
      isShowModal,
      fileFormats,
      selectedFormat,
      closeModal,
      handleExport
    }
  }
})
</script>
