<template>
  <div>
    <div v-loading="loading.all">
      <Tab :items="brandsTab" @scroll-end="onScrolledTab"/>
    </div>
    <transition name="fade">
      <Alert v-if="backgroundProcessData.data_process_notification == 1" color="green" view="smooth" class="mb-5" dense>
        <div class="flex flex-wrap items-center justify-between">
          <div>
            <p>
              Total New Publisher:
              <b>{{ backgroundProcessData.last_total_category_insert || 0 }}</b>
            </p>
            <p>
              Total New ISBN:
              <b>{{ backgroundProcessData.last_total_data_insert || 0 }}</b>
            </p>
          </div>
          <div class="flex flex-wrap items-center">
            <p class="mx-2">
              Last Updated:
              <b>{{
                backgroundProcessData.last_update_data
                  ? moment(
                      backgroundProcessData.last_update_data,
                      'YYYY-MM-DD HH:mm:ss'
                    ).format(me.datetime_format)
                  : ''
              }}</b>
            </p>
            <Button
              v-if="backgroundProcessData.last_total_data_invalid > 0"
              @click="
                $router.push({
                  name: 'settingBuybackDeviceTab',
                  params: {
                    category: 'books'
                  },
                  query: {
                    isbn: 'invalid'
                  }
                })
              "
              class="mx-2"
              dense
              color="red"
            >
              Show Invalid Data
            </Button>
            <Button @click="handleCloseNotification" :loading="loadingISBN.closeResultNotification || loadingUser.me" color="teal" view="outlined" class="mx-2" dense><i class="material-icons">close</i> Close Notification</Button>
          </div>
        </div>
        <template #left>
          <span class="material-icons text-4xl">info</span>
        </template>
      </Alert>
    </transition>
    <div class="flex items-start gap-4">
      <div class="flex-auto">
        <Input v-model="data.search" :disabled="priceUploadProcess || loading.all" placeholder="Search by brand name" class="w-full mb-5" icon block>
          <template #icon>
            <span class="material-icons">search</span>
          </template>
        </Input>
      </div>
      <div class="flex flex-auto justify-end gap-4">
        <Button :disabled="priceUploadProcess || loading.all" color="white" @click="openNetworkSetting">
          <div class="flex items-center text-teal-03">
            <div
              class="svg-load h-6 w-6 ml bg-teal-03"
              :style="`-webkit-mask-image: url(${origin}/icon/settings/signal.svg)`"
            ></div>
            <span class="inline-block ml-4">Cell Carriers Settings</span>
          </div>
        </Button>
        <Button :disabled="priceUploadProcess || loading.all" @click="exportData.show = true" color="white">
          <span class="text-teal-03"
            ><i class="material-icons mr-2">download</i>Export</span
          >
        </Button>
      </div>
    </div>
    <div v-loading="loading.all">
      <template v-if="items.length > 0">
        <DeviceTable
          :data="items"
          :category="category"
          :disabled="priceUploadProcess"
          @update-buyback="updateBuybackDevice"
          @update-buyback-category="updateBuybackDeviceByCategory"
          @update-all="updateAllBuybackDevice"
        />
      </template>
      <template v-else>
        <DeviceTable
          :data="[]"
          :category="{}"
        />
      </template>
      <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="state" />
    </div>
    <DeviceExportActiveData
      v-model:show="exportData.show"
      v-model:format="exportData.format"
      :loading="exportData.loading"
      @export="handleExportActiveCatalog"
      :disabled="isDisabledExportProductCatalog"
      disabled-reason="The data export process is in progress"
    />
  </div>
</template>
<script>
import { defineComponent, watch, computed, onUnmounted, inject } from 'vue'
import { useBuybackDevice } from 'composable/setting'
import { useISBN } from 'composable/product/book'
import { useUser } from 'composable/user'
import { usePaywall } from 'composable/paywall'
import { processing as processingPriceImport } from 'composable/pricing'
import { useRoute } from 'vue-router'
import DeviceTable from 'components/BuybackDevice/DeviceTable.vue'
import debounce from 'utils/debounce'
import Pagination from 'components/Pagination.vue'
import DeviceExportActiveData from 'components/BuybackDevice/DeviceExportActiveData.vue'
import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import ExpandablePanel from 'bbUi/components/expandable-panel/expandable-panel.vue'
import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import { ElMessage } from 'element-plus'
import { deepClone } from 'utils/deep-clone'
import { titleCase } from 'utils/string'

import moment from 'moment'

export default defineComponent({
  components: {
    Card,
    Checkbox,
    ExpandablePanel,
    Breadcrumb,
    Input,
    Button,
    Pagination,
    DeviceExportActiveData,
    DeviceTable,
    Tab,
    Alert
  },
  emits: ['showNetworkSetting'],
  async setup(props, { emit }) {
    const { data, exportData, state, brandsTab, loading, actionAll, actionUpdate, actionExportActiveCatalog, onLoadMoreTabs, loadingTabBrand } = useBuybackDevice()
    const { state: stateISBN, loading: loadingISBN, closeResultNotification } = useISBN()
    const { backgroundProcessData, action: actionMe, loading: loadingUser, me, isProPlan } = useUser()
    const { open: openPaywall } = usePaywall()
    const route = useRoute()

    const openNetworkSetting = inject('openNetworkSetting', () => {})

    const origin = computed(() => window.location.origin)
    const items = computed(() => {
      if(!state.value.data) return []
      const newData = Object.entries(deepClone(state.value.data)).map(([_, item]) => item).flat()
      return newData.map(item => ({
        ...item,
        child: []
      }))
    })
    const category = computed(() => state.value ? ({
      name: route.params.category,
      is_enable: state.value.is_enable,
      is_mac: route.params.category ? ( route.params.category.toLowerCase() === 'mac' ? 1 : 0 ) : 0
    }) : null)
    const isDisabledExportProductCatalog = computed(() => {
      return stateISBN.latestUploadedFile.is_export_product_catalog == 1
    })
    const priceUploadProcess = computed(() =>
      processingPriceImport.value.uploadProgress > 0 &&
      processingPriceImport.value.uploadProgress < 100
    )

    const breadcrumb = [{
      text: 'Settings',
      to: {
        name: 'setting'
      }
    }, {
      text: 'Catalog'
    }]

    
    const unWatch = watch(() => ({
      limit: data.limit,
      page: data.page,
      route: route.params,
      search: data.search
    }), debounce(async () => {
      if(route.name === 'settingBuybackDeviceBrand'){
        await actionAll(route.params.category, route.params.brand == 'all' ? null : route.params.brand)
      }
    }, 500), {
      deep: true
    })

    watch(() => ({
      limit: data.limit,
      route: route.params
    }), () => {
      data.page = 1
      data.search = ''
    }, {
      deep: true
    })

    onUnmounted(() => unWatch())

    const showIntercom = () => {
      window.CustomerSupport.use('show')
    }
    const updateAllBuybackDevice = debounce(async() => await actionAll(route.params.category, route.params.brand == 'all' ? null : route.params.brand, null), 1000)

    const updateBuybackDevice = async (item, callback) => {
      const { category } = route.params
      const { id, is_mac, is_enable, manufacturer, total_active } = item
      await actionUpdate({
        category_id: id,
        is_mac,
        is_enable,
        manufacturer,
        category,
        ...(total_active <= 1 && !is_mac && { is_forced: true })
      }, (res) => {
        if (res.status_code == 200) {
          ElMessage({
            type: 'success',
            message: 'Item status updated'
          })
        }
        typeof callback === 'function' && callback(res)
      }, true)
    }
    const updateBuybackDeviceByCategory = async (category, errCallback) => {
      const categoryName = category.name ? titleCase(category.name) : ''
      const isEnable = category.is_enable
      const isMac = category.is_mac
      await actionUpdate({
        category: categoryName,
        is_enable: isEnable,
        is_mac: isMac
      }, async(err) => {
        if(err) {
          errCallback(err)
        } else {
          await actionAll(route.params.category, route.params.brand == 'all' ? null : route.params.brand)
          ElMessage({
            type: 'success',
            message: 'Item status updated'
          })
        }
      })
    }
    const handleCloseNotification = async () => {
      await closeResultNotification(async (err) => {
        if(err) return
        await actionMe()
      })
    }
    const handleExportActiveCatalog = async () => {
      const isNotProPlan = !isProPlan.value
      if (isNotProPlan) {
        openPaywall({
          title: 'Export Catalog Data by CSV, XML, and API',
          description:
            'Easily export your product catalog data, including category structure and item titles, in CSV, XML, or via our API. Seamlessly integrate this data into your buyback platform to power your catalog and streamline the trade-in process.',
          image_url: '/images/paywall/paywall-export-product-catalog.svg'
        })
        return
      }
      await actionExportActiveCatalog()
      exportData.show = false
      ElMessage({
        message: 'Your data is being exported.',
        type: 'success'
      })
    }
    const onScrolledTab = () => onLoadMoreTabs()
    
    await actionAll(route.params.category, route.params.brand == 'all' ? null : route.params.brand)

    return {
      state,
      loading,
      loadingISBN,
      loadingUser,
      me,
      brandsTab,
      isDisabledExportProductCatalog,
      breadcrumb,
      data,
      exportData,
      origin,
      items,
      category,
      showIntercom,
      updateBuybackDevice,
      updateBuybackDeviceByCategory,
      updateAllBuybackDevice,
      backgroundProcessData,
      handleCloseNotification,
      handleExportActiveCatalog,
      openNetworkSetting,
      moment,
      priceUploadProcess,
      onScrolledTab,
      loadingTabBrand
    }
  }
})
</script>
<style scoped lang="scss">
.svg-load {
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}
</style>